* {
    margin: 0;
    padding: 0;
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
    list-style: none;
    text-decoration: none;
    scroll-behavior: smooth;
  }
  
  body {
    font-family: "Montserrat", sans-serif;
  }
  
  /*MOBILE*/
  @media (max-width: 720px) {
  
    body {
      background-color: #fafafa;
    }
  
    .navigation {
      background-color: white;
  
      display: flex;
      align-items: center;
      justify-content: space-between;
  
      padding: 12px;
    }
  
    .navigation svg {
      width: 100px;
    }
  
    .navigation .menu-list {
      height: 30vh;
      width: 100vw;
      position: absolute;
      top: 0;
      right: 0;
  
      display: none;
      justify-content: center;
      align-items: center;
  
      background: white;
      flex-direction: column;
      box-shadow: 0px 4px 12px rgba(134, 208, 203, 0.05), 0px 18px 31px rgba(100, 100, 100, 0.38);
    }
  
    .navigation .menu-list .menu-item {
      display: block;
    }
  
    .navigation .menu-list .menu-item a {
      font-size: 20px;
      color: black;
    }
  
    .navigation .menu-list .menu-item a:hover {
      color: #fa4a0c;
    }
  
    .openMenu,
    .closeMenu {
      cursor: pointer;
      font-size: 30px;
      color: #737373;
    }
  
    .closeMenu{
      margin-bottom: 20px;
      color: red;
    }
  
    .introduce {
      display: flex;
      align-items: center;
      justify-content: center;
      flex-direction: column;
      text-align: center;
      padding: 20px;
    }
  
    .introduce h2 {
      font-size: 36px;
      letter-spacing: 0.2px;
      margin: 15px 0;
    }
  
    .introduce h4 {
      color: rgb(128, 128, 128);
      font-size: 24px;
      font-weight: 100;
    }

    
  
    .link-group {
      display: flex;
      align-items: center;
      justify-content: center;
      flex-direction: column;
      margin: 20px auto;
    }
  
    .link-group a {
      padding: 20px;
      background: none;
      font-size: 24px;
  
      border-radius: 30px;
      width: 250px;
      margin: 5px auto;
  
      border: 1px solid #fa4a0c;
  
      color: #fa4a0c;
    }
  
    .link-group a:hover {
      color: white;
      background-color: #fa4a0c;
    }
  
    .demonstration {
      padding: 20px;
      display: flex;
      align-items: center;
      justify-content: center;
    }
  
    .demonstration img {
      border-radius: 60px;
    }
  
    hr {
      border: 2px solid gainsboro;
      order: 5;
    }
  
    .title {
      margin-top: 20px;
      text-align: center;
      font-size: 15px;
    }
  
    .steps .row {
      text-align: center;
      margin: 50px 30px;
    }
  
    .steps .row h3 {
      color: #fa4a0c;
    }
  
    .steps .row h2 {
      font-size: 40px;
      letter-spacing: 0.2px;
      margin: 15px 0;
    }
  
    .steps .row h4 {
      color: #737373;
      font-size: 24px;
      letter-spacing: 0.2px;
      font-weight: 100;
    }
  
    .steps .row:nth-child(1) {
      display: flex;
      align-items: center;
      justify-content: center;
      flex-direction: column-reverse;
    }
  
    .steps .row:nth-child(3) {
      display: flex;
      align-items: center;
      justify-content: center;
      flex-direction: column-reverse;
    }
  
    .download {
      background-color: #252b42;
      color: white;
      padding: 20px;
      text-align: center;
      display: flex;
      align-items: center;
      justify-content: center;
      flex-direction: column;
      height: 140vh;
    }
  
    .download h4 {
      display: none;
    }
  
    .download h2 {
      font-size: 40px;
      text-align: center;
    }
  
    .download h2::after {
      content: "Most calendars are designed for teams.";
      display: block;
      margin-top: 17px;
      position: static;
      font-size: 24px;
      text-align: center;
      font-weight: 500;
      color: rgb(143, 140, 140);
    }
  
    .download .link-group {
      flex-direction: row;
    }
  
    .download .link-group a {
      max-width: 200px;
      color: rgb(255, 255, 255);
      border: 1px solid white;
      border-radius: 15px;
      margin: 0 5px;
      font-size: 16px;
      display: relative;
    }
  
    .download .link-group a:hover {
      background-color: #fa4a0c;
      border: 1px solid #fa4a0c;
    }
  
    footer {
      margin-bottom: 20px;
      padding: 25px;
      text-align: center;
    }
  
    footer a {
      color: #fa4a0c;
    }
  
    footer > ul {
      display: flex;
      align-items: center;
      justify-content: center;
    }
  
    footer ul li {
      margin: 15px 20px;
      font-size: 24px;
      color: tomato;
    }
  
    footer svg,
    p {
      display: none;
    }
  
    footer::after {
      content: "Don't stop 'til you proud! üòÅ";
      font-size: 15px;
    }
  }
  
